import React, { useEffect, useReducer } from "react";

import {
  getDefaultFavorites,
  getFavoritesWithExRates,
  sanitizeInputAmount,
  T_CurrencyExData
} from "models/currencyEx";
import { T_Nullable, T_Object } from "types/generic";
import {
  fetchCurrencyExData,
  fetchInputCodeSync,
  fetchTargetCodeSync
} from "helpers/api/currencyEx";

export function useCurrencyExReducer(): [
  T_CurrencyExState,
  React.Dispatch<T_CurrencyExAction>
] {
  const [state, dispatch] = useReducer<
    React.Reducer<T_CurrencyExState, T_CurrencyExAction>
  >(currencyExReducer, {
    inputCode: fetchInputCodeSync(),
    targetCode: fetchTargetCodeSync(),
    inputAmount: "",
    favorites: getDefaultFavorites() // TODO: get from localstorage
  });
  useCurrencyExData(dispatch);

  return [state, dispatch];
}

const currencyExReducer = (
  state: T_CurrencyExState,
  [type, payload]: T_CurrencyExAction
) => {
  switch (type) {
    case T_CurrencyExActionTypes.setData:
      return {
        ...state,
        conversionRates: payload
      };
    case T_CurrencyExActionTypes.setInputCode:
      return {
        ...state,
        inputCode: payload,
        favorites:
          state.conversionRates && Number(state.inputAmount) > 0
            ? getFavoritesWithExRates(
                state.favorites,
                state.conversionRates,
                payload,
                state.inputAmount
              )
            : state.favorites
      };
    case T_CurrencyExActionTypes.setTargetCode:
      return {
        ...state,
        targetCode: payload
      };
    case T_CurrencyExActionTypes.setInputAmount:
      const sanitizedInputAmount = sanitizeInputAmount(payload);

      // Avoid state update (and re-render) if there is no change
      if (sanitizedInputAmount === state.inputAmount) {
        return state;
      }

      return {
        ...state,
        inputAmount: sanitizedInputAmount,
        favorites: state.conversionRates
          ? getFavoritesWithExRates(
              state.favorites,
              state.conversionRates,
              state.inputCode,
              sanitizedInputAmount
            )
          : state.favorites
      };
    case T_CurrencyExActionTypes.setCurrencyToValue:
      return {
        ...state,
        currencyToValue: payload
      };
    case T_CurrencyExActionTypes.toggleFavorite: {
      const favoritesArrWithoutPayloadItem = state.favorites.filter(payload);

      // Check if the filtering resulted in a smaller array than state.favorites
      if (favoritesArrWithoutPayloadItem.length < state.favorites.length) {
        // This means that the supplied item already existed in state.favorites
        // and we just obtained an array after removing it.
        return {
          ...state,
          favorites: favoritesArrWithoutPayloadItem
        };
      }

      // Otherwise it means that payload item is a new item to be favorited
      return {
        ...state,
        favorites: [...state.favorites, payload]
      };
    }
    default:
      return state;
  }
};

function useCurrencyExData(dispatch: React.Dispatch<T_CurrencyExAction>) {
  useEffect(() => {
    const fetchData = async () => {
      let result: T_CurrencyExData | null = await fetchCurrencyExData();

      if (result) {
        dispatch([T_CurrencyExActionTypes.setData, result.conversionRates]);
      }
    };

    fetchData();
  }, []);
}

/**
 * Types
 */
export const enum T_CurrencyExActionTypes {
  setData,
  setInputCode,
  setTargetCode,
  setInputAmount,
  setCurrencyToValue,
  toggleFavorite
}

export type T_FavoriteWithExRate = {
  currencyCode: string;
  exRate: T_Nullable<number>;
};

type T_CurrencyExAction = [type: T_CurrencyExActionTypes, payload: any];

type T_CurrencyExState = {
  conversionRates?: T_Object<number>;
  inputCode: string;
  targetCode: string;
  inputAmount: string;
  favorites: T_FavoriteWithExRate[];
};
