import CONST from "constants/app";
import * as CONST_CURRENCY_EX from "constants/currencyEx";
import { getTimetamp } from "helpers/time";
import { LocalStorage } from "helpers/webStorage";
import {
  mapCurrencyExRateResponse,
  T_CurrencyExData,
  T_CurrencyExResponse
} from "models/currencyEx";
import request, { T_HTTPMethod } from "../request";

export async function fetchCurrencyExData() {
  let result: T_CurrencyExData | null = JSON.parse(
    LocalStorage.getItem(CONST.webStorage.currencyExData) ?? "null"
  );

  // Make the API call only when necessary
  if (
    !result || // if there is no cached data yet
    getTimetamp() > result.timeNextUpdate // // OR if cached data is expired
  ) {
    const response = await request<T_CurrencyExResponse>({
      method: T_HTTPMethod.GET,
      endpoint: CONST.API.currencyEx
    });
    if (response.result) {
      result = mapCurrencyExRateResponse(response.result);
      // Update cache with latest data
      LocalStorage.setItem(
        CONST.webStorage.currencyExData,
        JSON.stringify(result)
      );
    }
  }

  return result;
}

export function fetchInputCodeSync() {
  return (
    LocalStorage.getItem(CONST.webStorage.currencyExInputCode) ||
    CONST_CURRENCY_EX.defaultTargetCode
  );
}

export function fetchTargetCodeSync() {
  return (
    LocalStorage.getItem("currencyTargetCode") ||
    CONST_CURRENCY_EX.defaultTargetCode
  );
}
