import * as CONST_CURRENCY_EX from "constants/currencyEx";
import { T_FavoriteWithExRate } from "pages/Tools/Money/CurrencyEx/hooks";
import { T_Nullable, T_Object } from "types/generic";

export function mapCurrencyExRateResponse(response: T_CurrencyExResponse) {
  return {
    baseCode: response.base_code,
    conversionRates: response.conversion_rates,
    timeLastUpdate: response.time_last_update_unix,
    timeNextUpdate: response.time_next_update_unix
  };
}

export function sanitizeInputAmount(value: string) {
  return value
    .replace(/[^0-9.]/g, "") // allow only number
    .replace(/(\..*?)\..*/g, "$1"); // allow only one decimal
}

export function getFavoritesWithExRates(
  favorites: T_FavoriteWithExRate[],
  conversionRates: T_Object<number>,
  inputCode: string,
  inputAmount: string
) {
  return favorites.map((favItem) => ({
    ...favItem,
    exRate: calculateExRate(
      conversionRates,
      inputCode,
      favItem.currencyCode,
      Number(inputAmount)
    )
  }));
}

export function getDefaultFavorites() {
  return CONST_CURRENCY_EX.defaultFavorites.map((code) => ({
    currencyCode: code,
    exRate: null
  }));
}

/**
 * Assume:
 *    base currency code (from API): USD
 *    input currency code: EUR
 *    input amount: x
 *    target currency code: YYY
 *
 * and we know (from API):
 *    1 USD = n1 EUR
 *    1 USD = n2 YYY
 *
 * then,
 *               1 EUR  =  (1 / n1) USD
 *               x EUR  =  x * (1 / n1) USD
 *    x * (1 / n1) USD  =  n2 * x * (1 / n1) YYY
 *               x EUR  =  n2 * x * / n1 YYY
 */
function calculateExRate(
  conversionRates: T_Object<number>,
  inputCode: string,
  targetCode: string,
  inputAmount: number
) {
  const x = inputAmount;
  const n1 = conversionRates[inputCode];
  const n2 = conversionRates[targetCode];

  return +((n2 * x) / n1).toFixed(2);
}

/**
 * Types
 */
export interface T_CurrencyExResponse {
  conversion_rates: T_Object<number>;
  base_code: string;
  time_last_update_unix: number;
  time_last_update_utc: string;
  time_next_update_unix: number;
  time_next_update_utc: string;
}

export type T_CurrencyExData = ReturnType<typeof mapCurrencyExRateResponse>;

export type T_CurrencyMapWithRates = T_Object<
  CONST_CURRENCY_EX.T_CurrencyMapItem & { conversionRate: T_Nullable<number> }
>;
